import React, {useState} from 'react'
import "../styles/About.css"

function About() {
  const [visible1, setVisible1] = useState(true);
  const [visible2, setVisible2] = useState(false);
  const [visible3, setVisible3] = useState(false);
  const clickNext = () =>{
    if(visible1){
      setVisible1(false);
      setVisible2(true);
      setVisible3(false);
    }else if(visible2){
      setVisible1(false);
      setVisible2(false);
      setVisible3(true);
    }else{
      setVisible1(true);
      setVisible2(false);
      setVisible3(false);
    }
  }

  const clickPrev = () =>{
    if(visible1){
      setVisible1(false);
      setVisible2(false);
      setVisible3(true);
    }else if(visible2){
      setVisible1(true);
      setVisible2(false);
      setVisible3(false);
    }else{
      setVisible1(false);
      setVisible2(true);
      setVisible3(false);
    }
  }

  return (
    <div className='mainsection'>
        <div className='box'>
          <div><input type="button" value="<" onClick={clickPrev} className="corouselbutton"></input></div>
          <div className="contentarea">
            {visible1 && <article >
                  <h2>Who Am I?</h2>
                  <p>I am a tech enthusiast and I love to explore latest technologies</p>
                  <p>Although I did my undergraduation in mechanical engineering after getting an opportunity in TCS as a software engineer, I worked hard and learned many technologies and I try to improve my self every day</p>
                  <p>I have studied DBMS, Computer Networks, Datastructures and Algorithms on my own. Today I am as good as any computer science graduate</p>
                  <p>Now, software Engineering has become my passion and want to spread my wings in this field</p>
            </article>}
            {visible2 && <article>
                <h2>Experience</h2>
                <h3>Tata Consultancy Services - Systems Engineer</h3>
                <p>2018 - Present</p>
                <p>my role at TCS is Identity and Access Management analyst and my resposnibilities include:</p>
                <p>Promotes cooperation and demonstrates patience in working with customers, peers, and other resources</p>
                <p>Identifies and negotiates own development opportunities</p>
                <p>Plans own work to meet given objectives and processes</p>
                <p>Rapidly absorbs new technical and security related information and applies it effectively</p>
                <p>Works effectively on multiple assignments, simultaneously, in a fast-paced and highly challenging environment</p>
                <p>Demonstrates moderate problem solving, decision-making, and functional knowledge</p>
                <p>Demonstrates awareness of key security trends</p>
                <p> Stays abreast of the systems/applications change management process</p>
                <p> Communicates to management any changes that impact the security of the application</p>    
            </article>}
           {visible3 && <article>
                <h2>Education</h2>
                <h3>Velagapudi Ramakrishna Siddhartha Engineering College</h3>
                <p>Mechanical Engineering</p>
                <p>2014 - 2018</p>
                <p>CGPA: 8.5</p>
            </article>}
            </div>
          <div><input type="button" value=">" onClick={clickNext} className="corouselbutton"></input></div>
        </div>
    </div>
  )
}

export default About
