import React,{useState} from 'react'
import "../styles/Projects.css"

function Projects() {
    const [visible1, setVisible1] = useState(true);
  const [visible2, setVisible2] = useState(false);
  const [visible3, setVisible3] = useState(false);
  const clickNext = () =>{
    if(visible1){
      setVisible1(false);
      setVisible2(true);
      setVisible3(false);
    }else if(visible2){
      setVisible1(false);
      setVisible2(false);
      setVisible3(true);
    }else{
      setVisible1(true);
      setVisible2(false);
      setVisible3(false);
    }
  }

  const clickPrev = () =>{
    if(visible1){
      setVisible1(false);
      setVisible2(false);
      setVisible3(true);
    }else if(visible2){
      setVisible1(true);
      setVisible2(false);
      setVisible3(false);
    }else{
      setVisible1(false);
      setVisible2(true);
      setVisible3(false);
    }
  }

  return (
    <div className='mainsection'>
        <div className='box'>
          <div><input type="button" value="<" onClick={clickPrev} className="corouselbutton"></input></div>
          <div className="contentarea">
            {visible1 && <article >
                <h2>Application</h2>
                <p>A small Banking Web Application</p>
                <p>This was my first application created. It is a banking web application using J2EE and MVC Architecture. Used servelets, JDBC,
                JSP, My SQL and Apache tomcat server</p>
                <p>The function of the application is to provide an interface for
                bank employees to register new customers and deposit, withdrawal interface for cashier</p>
                  
            </article>}
            {visible2 && <article>
                <h2>Automation</h2>
                <p>created a macro using VBA which reduced the time to complete the task from 10
                    minutes to less than 5 seconds in Excel using REGEX. This saved 3 man hours every month</p>
            </article>}
           {visible3 && <article>
                <h2>Static Web App</h2>
                <p>The website you are currently looking at is Live example of my work</p>
                <p>This is website is created using React.js</p>
                <p>Used few libraries to optimise the application, for example I used react-form-hook which reduces the rendering frequesncy and reduces client side processing power usage</p>
                <p>Used an external service for email notification in the contact component</p>
            </article>}
            </div>
          <div><input type="button" value=">" onClick={clickNext} className="corouselbutton"></input></div>
        </div>
    </div>
  )
}

export default Projects
