import React, {useState} from 'react';
import '../styles/Contact.css';
import {useForm} from 'react-hook-form';
import emailjs from 'emailjs-com'

function Contact() {
    const { handleSubmit, register } = useForm();
    const [isSending, setIsSending] = useState(false);
    const [isSent, setIsSent] = useState(false);
    const [isSedningError, setIsSendingError] = useState(false);
    const onSubmit = (data,e) => {
        setIsSending(true);
        /*user_5BElXUwV6WyGla9wUnZmg*/
        emailjs.send("service_rzbkdp4", "template_g53t30s", data, "user_5BElXUwV6WyGla9wUnZmg")
      .then((result) => {
          console.log(result.text);
        }, (error) => {
          console.log(error.text);
          if(error.text!==""){
              setIsSending(false);
                setIsSendingError(true);
                setTimeout(()=>setIsSendingError(false),2000);
            }}).then((isSedningError)=>{
            if(!isSedningError){
                setIsSending(false);
                setIsSent(true);
                setTimeout(()=>{
                    setIsSent(false)}
                    ,2000);
            }})
        e.target[0].value = '';
        e.target[1].value = '';
        e.target[2].value = '';
    }
    return (
        <>
            <div className='contactdetails'>
                <a href="mailto:yejju.ritesh@gmail.com">Email: yejju.ritesh@gmail.com</a><br/>
                <a href="tel:+91 8978025163">Phone: +91 8978025163</a>
            </div>
            <div className='contactform'>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <input {...register("fullname") } placeholder = "Your Name..." className='inputblock1' required/>
                    <input {...register("email")} placeholder = "Your Email..." className='inputblock2' type="email" required/>
                    <textarea {...register("message")} placeholder = "Your Message..." className='inputblock3' type="text" required/>
                    <input type="submit" className='button' value="Send Message"/>
                </form>
            </div>
            {isSending && <div className='loadingmessage'>
                <p>Sending Message...</p>
            </div>}
            {isSent && <div className='loadingmessage'>
                <p>Your Message is Sent..!</p>
            </div>}
            {isSedningError && <div className='loadingmessage'>
                <p>Error Sending Message...</p>
            </div>}
        </>
    )
}

export default Contact
