import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import '../styles/Navbar.css'

const Navbar = () => {
    const [toggle,setToggle] = useState("nav__cont__close");
    const toggleFunction = () =>{
        if(toggle==="nav__cont__close"){
            setToggle("nav__cont__open");
        }else{
            setToggle("nav__cont__close");
        }
    }
return (
    <nav className ={toggle} onClick={toggleFunction}>
        <div className="menu">
            <p>MENU</p>
        </div>
        <ul className = "nav">
            <li className="nav__items">
                <Link to='/home' className="navbtn">Home</Link>
            </li>
            <li className="nav__items">
                <Link to='/about' className="navbtn">About</Link>
            </li>
            <li className="nav__items">
                <Link to='/skills' className="navbtn">Skills</Link>
            </li>
            <li className="nav__items">
                <Link to='/projects' className="navbtn">Projects</Link>
            </li>
            <li className="nav__items">
                <Link to='/contact' className="navbtn">Contact</Link>
            </li>
        </ul>
    </nav>
  );
};

export default Navbar;
